import React  from 'react'
import Home from './Home';
import {default as FakeStore } from 'apps/fake_store'
import {default as PokemonSearch } from 'apps/pokemon_search'
import {default as Todo } from 'apps/todo_context_ts/'
import {default as Tirtagracia } from 'apps/tirtagracia/Tirtagracia'

import { BrowserRouter, Route, Switch } from 'react-router-dom'
import './app.scss'
import Navbar from 'components/Navbar';

function App() {

    return (
        <div className="App">

            <BrowserRouter>
                <Navbar />
                <div className="container">
                    <Switch>
                        <Route path="/fake-store" component={FakeStore} />
                        <Route path="/pokemon-search" component={PokemonSearch} />
                        <Route path="/todo-context-reducer-ts" component={Todo} />
                        <Route path="/tirtagracia" component={Tirtagracia} />
                        <Route path="/" component={Home} />
                    </Switch>
                </div>
            </BrowserRouter>
        </div>
    );
}

export default App;
